@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@200&display=swap);
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --primary: rgb(80, 80, 80);
  --secondary: blue;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Assistant', sans-serif;
  font-size: 3.5rem;
  letter-spacing: 4px;
  margin-bottom: 10px;
}

.menu {
  display: flex;
  justify-content: space-evenly;
  width: 600px;
  height: 20px;
  font-size: 1rem;
  font-weight: 100;
}

nav a {
  text-decoration: none;
  color: rgb(80, 80, 80);
  color: var(--primary);
}

.active {
  border-bottom: 2px solid rgb(80, 80, 80);
  border-bottom: 2px solid var(--primary); 
  cursor: pointer;
}

/* .menu-item:hover {
  border-bottom: 2px solid var(--primary); 
  cursor: pointer;
} */

.title:hover {
  cursor: pointer;
}

.hero {
  height: auto;
  max-width: 990px;
  margin: auto;
}

.hero img {
  max-height: 600px;
  width: 100%;
  object-fit: contain;
}

.grid-container {
  margin-top: 50px;
  width: 100vw;
  max-width: 1080px;
}

.grid {
  display: grid;
  justify-items: center;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 500px);
  grid-template-rows: 1fr;
  grid-gap: 20px;
}

.grid-item {
  position: relative;
  width: 500px;
}

.grid-item img {
  display: block;
  width: 100%;
  height: auto;
  opacity: 1;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.grid-item-overlay {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  width: 500px;
  height: 100%;
  z-index: 100;
}

.grid-item-overlay-text {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items:center;
  color: white;
  font-size: 10rem;
}

.grid-item img:hover {
  opacity: .3;
}

.grid-item-overlay:hover {
  opacity: 1;
}

.footer-container {
  color: rgb(80, 80, 80);
  color: var(--primary);
  margin: 30px;
}

@media (max-width: 600px) {
  .menu {
    width: 100vw;
  }
}
